body {
  background-color: #1a2436;
}

h1 {
  font-family: "Ubuntu", sans-serif;
  color: white;
  font-size: large;
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
}

.store {
  width: 100%;
  margin-left: auto;
  margin-right: auto;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.store-item {
  max-width: 45%;
  padding: 5px;

  & h3 {
    font-family: "Ubuntu", sans-serif;
    font-size: medium;
    font-weight: bold;
    color: #6285bc;
    margin-top: 8px;
    margin-bottom: 0;
    padding-left: 3px;
  }

  & p {
    font-family: "Ubuntu", sans-serif;
    font-size: medium;
    font-weight: bold;
    color: #6285bc;
    margin-top: 5px;
    padding-left: 3px;
    margin-bottom: 0;
  }

  & p::after {
    content: " ₽";
  }

  & button {
    background-color: #6285bc;
    border: none;
    border-radius: 10px;
    font-family: "Ubuntu", sans-serif;
    font-size: medium;
    font-weight: bold;
    color: #041e47;
    white-space: nowrap;
    margin-bottom: 0;
    margin-top: 6px;
    padding: 5px;
  }
}

img {
  max-width: 100%;
  border-radius: 10px;
  display: block;
  margin: 0;
  padding: 0;
}

.product {
  width: 100%;
  padding: 15px 0 15px 0;

  & .image-container {
    position: relative;
    background-color: none;
    overflow: hidden;
    width: 95%;
    margin-left: auto;
    margin-right: auto;
  }

  & h1 {
    margin-bottom: 10px;
    width: 70%;
    padding: 0;
    text-align: center;
  }

  & ul {
    margin: 0;
    padding: 0;
  }

  & .container {
    background-color: #6285bc;
    padding: 15px;
    border: none;
    border-radius: 8px;
    margin-left: 8px;
    margin-right: 8px;
  }

  & .price::after{
    content: " ₽";
  }

  & .price-container{
    width: 95%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: space-between;



  }

  & p,
  span,
  li,
  button {
    text-align: center;
    font-family: "Ubuntu", sans-serif;
    font-size: medium;
    font-weight: bold;
    color: #041e47;
  }

  & p {
    font-size: small;
  }

  & h3{
    margin-bottom: 8px;
    margin-top: 0;
  }

  & ul {
    padding-left: 15px;
  }

  & li {
    text-align: left;
  }
}


i{
  color:white;
}